<template>
  <div id="app">
    <div class="agreement" v-if="!isAgree">
      <p class="argeement-condition">
        Я розумію що данний сайт НЕ є достовірним джерелом інформації. 
      </p>
      <button @click="agree" class="argeement-button">Згоден перевіряти усі питання власноруч</button>
    </div>

    <div class="test" v-if="isAgree">
      <h1 class="test_name">{{testData[secretNumber].question}}</h1>
      <button @click="checkAnswer(testData[secretNumber].answers.a, 'a')" v-bind:class="[letterOfRightAnswer == 'a' ? 'button-true' : '', letterOfRightAnswer != 'a' && letterOfRightAnswer != '' ? 'button-false':'','test_answer']">{{testData[secretNumber].answers.a}}</button>
      <button @click="checkAnswer(testData[secretNumber].answers.b, 'b')" v-bind:class="[letterOfRightAnswer == 'b' ? 'button-true' : '', letterOfRightAnswer != 'b' && letterOfRightAnswer != '' ? 'button-false':'','test_answer']">{{testData[secretNumber].answers.b}}</button>
      <button @click="checkAnswer(testData[secretNumber].answers.c, 'c')" v-bind:class="[letterOfRightAnswer == 'c' ? 'button-true' : '', letterOfRightAnswer != 'c' && letterOfRightAnswer != '' ? 'button-false':'','test_answer']">{{testData[secretNumber].answers.c}}</button>
      <button @click="checkAnswer(testData[secretNumber].answers.d, 'd')" v-bind:class="[letterOfRightAnswer == 'd' ? 'button-true' : '', letterOfRightAnswer != 'd' && letterOfRightAnswer != '' ? 'button-false':'','test_answer']">{{testData[secretNumber].answers.d}}</button>
      <button @click="checkAnswer(testData[secretNumber].answers.e, 'e')" v-bind:class="[letterOfRightAnswer == 'e' ? 'button-true' : '', letterOfRightAnswer != 'e' && letterOfRightAnswer != '' ? 'button-false':'','test_answer']">{{testData[secretNumber].answers.e}}</button>
    </div>
    <button v-if="isAgree" @click="showStat" class="stat_button">Звіт</button>
    <button v-if="isAgree" @click="backQuestion" class="back_question arrows">⟸ Назад</button>
    <button v-if="isAgree" @click="nextQuestion" class="next_question arrows">Вперед ⟹</button>
    <button v-if="isAgree" @click="nextQuestion" class="next_question arrows">Вперед ⟹</button>

    <div v-if="isStat" @click="closeStat" class="stat"> 
      <div class="window">
        <h2 class="stat-text">Ваш результат:</h2>
        <p class="stat-text">В данному буклеті {{restOfQuestions}} питань</p>
        <p class="stat-text">Пройдено запитань:<br>{{secretNumber+1}}</p>
        <p class="stat-text">Відповіли правильно:<br>{{rightAnswers}}</p>
        <p class="stat-text">Допустили помилок:<br>{{wrongAnswers}} </p>
        <p v-if="rightAnswers||wrongAnswers" class="stat-text">Результат: {{((rightAnswers/(rightAnswers+wrongAnswers))*100).toFixed(1)}} %</p>
      </div>
    </div>
  </div>
</template>

<script>
import testData from './newData2019.json'

export default {
  name: 'App', 
  data() {
    return {
      isAgree: false, 
      testData,
      secretNumber: 0,
      isQuestionPassed: false,
      restOfQuestions: testData.length+1,
      rightAnswers: 0, 
      wrongAnswers: 0, 
      percentage: 0,
      letterOfRightAnswer: '', 
      isStat: false
    }
  }, 
  methods: {
    showStat() {
      this.isStat = !this.isStat; 
    },
    closeStat() {
      this.isStat = false; 
    }, 
    agree() {
      this.shuffle(this.testData); 
      this.isAgree = true;
    },
    shuffle(array) {
      var currentIndex = array.length, temporaryValue, randomIndex;
      // While there remain elements to shuffle...
      while (0 !== currentIndex) {
        // Pick a remaining element...
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex -= 1;
        // And swap it with the current element.
        temporaryValue = array[currentIndex];
        array[currentIndex] = array[randomIndex];
        array[randomIndex] = temporaryValue;
      }
      return array;
    }, 
    backQuestion() {
      if(this.secretNumber >0) {
        this.secretNumber -=1; 
       }  else alert('Ви на першому запитанні.'); 
    },
    nextQuestion() {
      if (this.secretNumber == 200){
        alert ('Тестування завершено.'); 
      } else {
         this.secretNumber +=1; 
         this.letterOfRightAnswer = '';
        }
    }, 
    checkAnswer(a) {
      this.isQuestionPassed = true; 
      this.restOfQuestions -= 1;
      if(a == this.testData[this.secretNumber].right_answer_full) {
        this.rightAnswers += 1; 
        this.letterOfRightAnswer = this.testData[this.secretNumber].right_answer; 
      } else {
        this.wrongAnswers += 1; 
        this.letterOfRightAnswer = this.testData[this.secretNumber].right_answer;
      }
    }
  }
}
</script>

